import React from 'react'
import BigNumber from 'bignumber.js'
import { Card, CardBody, Heading, Text } from '@pancakeswap-libs/uikit'
import styled from 'styled-components'
import { getBalanceNumber } from 'utils/formatBalance'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import useI18n from 'hooks/useI18n'
import { getCakeAddress } from 'utils/addressHelpers'
import { useFarms, usePriceCakeBusd } from 'state/hooks'
import CardValue from './CardValue'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`

const CakeStats = () => {
  const TranslateString = useI18n()
  const farms = useFarms();
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance(getCakeAddress())
  const cakeSupply = totalSupply ? getBalanceNumber(totalSupply) - getBalanceNumber(burnedBalance) : 0
  const honyPrice = usePriceCakeBusd();
  // const circSupply = totalSupply ? totalSupply.minus(burnedBalance) : new BigNumber(0);
  const marketCap = Number(honyPrice.times(cakeSupply));

  let honyPerBlock = 0;
  if(farms && farms[0] && farms[0].honyPerBlock){
    honyPerBlock = new BigNumber(farms[0].honyPerBlock).div(new BigNumber(10).pow(18)).toNumber();
  }

  return (
    <StyledCakeStats>
      <CardBody>
        <Heading size="xl" mb="24px">
          {TranslateString(534, 'HONY Stats')}
        </Heading>
        <Row>
          <Text fontSize="20px">{TranslateString(999, 'Total Market Cap in USD')}</Text>
          <CardValue fontSize="20px" decimals={2} value={marketCap} />
        </Row>
        <Row>
          <Text fontSize="17px">{TranslateString(536, 'Total HONY Supply')}</Text>
          {cakeSupply && <CardValue fontSize="14px" value={cakeSupply} />}
        </Row>
        <Row>
          <Text fontSize="17px">{TranslateString(538, 'Total HONY Burned')}</Text>
          <CardValue fontSize="17px" value={getBalanceNumber(burnedBalance)} />
        </Row>
        <Row>
          <Text fontSize="17px">{TranslateString(540, 'New HONY/block')}</Text>
          <CardValue fontSize="17px" decimals={2} value={honyPerBlock} />
        </Row>
      </CardBody>
    </StyledCakeStats>
  )
}

export default CakeStats
